export const useHostelFeeStructureList = (args) => {
  const { data, refetch, isFetching } = useHostelFeeStructureListQuery(
    args.data,
    {
      skip: args.skip,
    }
  );
  return {
    hostelFeeStructureList: data,
    hostelFeeStructureListRefetch: refetch,
    hostelFeeStructureListLoading: isFetching,
  };
};
export const useHostelFeeStructureAdd = () => {
  const [hostelFeeStructureAdd] = useHostelFeeStructureAddMutation();
  return [hostelFeeStructureAdd];
};
